.app-container {
  height:100vh;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: 200px 1fr;
  grid-template-areas:
    "h h"
    "n c";
  grid-gap: 10px;
  color: #fff;
}
.app-content-wrapper{
    grid-area: c;
    /* overflow: hidden; */
    color: #3f3f3f;
    position: relative;
}
/* .app-content-wrapper > *{
  height:100%;
  overflow: auto;
} */
a img, img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

